import React, { useEffect, useState } from 'react'
import '../styles/Carousel.css'
import img1 from "../assets/9.png";
import img2 from "../assets/11.png";
import img3 from "../assets/36.png";
import img4 from "../assets/125.png";
import img5 from "../assets/204.png";
import img6 from "../assets/237.png";

function Carousel() {
    const[animation,setAnimation]=useState({})
    const[opacity,setOpacity]=useState({})
    const[index,setIndex]=useState(0)
    let imgPercent=[210/(210*11)]*100;
    useEffect(()=>{
        let classes={};
        let imgPercent=[210/(210*11)]*100;
        let iP=(605+(600-((1115-630)/2)))/(605*11)*100;
  
        let width=document.getElementById('Carousel-Body').clientWidth;
        
        if(width===290){
            iP=16.72;
        }
        if(width===330){
            iP=15.85;
        }
    
        let p=iP;
        setOpacity({img6:{opacity:0.5},img2:{opacity:0.5}})
      
        classes={transform:`translateX(-${iP}%)` };
        setAnimation(classes)
        
        p+=imgPercent;
    
        setInterval(()=>{
            
            classes={transform:`translateX(-${p}%)`,transition: "transform 500ms ease-in-out 0s" };
            setAnimation(classes)
            setIndex(p)
            if(Math.floor(p)===(Math.floor(iP+(imgPercent*6)))){
            
                p=iP
                
                setTimeout(()=>{
                    
                    classes={transform:`translateX(-${iP}%)` };
                    setAnimation(classes)
                    setIndex(iP)
                    p+=imgPercent;
                    
                },[500])
                    
            }
            else{
               
                p+=imgPercent;
                
            }
            
        }, 1500);
    
        
    },[])
 
    useEffect(()=>{
        let iP=(605+(600-((1115-630)/2)))/(605*11)*100;
        
        let width=document.getElementById('Carousel-Body').clientWidth;
        
        if(width===290){
            iP=16.72;
        }
        if(width===330){
            iP=15.85;
        }
        
        switch(Math.floor(index)){
            case Math.floor(iP):
                setOpacity({img6:{opacity:0.4},img2:{opacity:0.4}})
                break;
            case Math.floor(iP+(imgPercent)):
                setOpacity({img1:{opacity:0.4},img3:{opacity:0.4}})
                break;
            case Math.floor(iP+(imgPercent*2)):
                setOpacity({img2:{opacity:0.4},img4:{opacity:0.4}})
                break;
            case Math.floor(iP+(imgPercent*3)):
                setOpacity({img3:{opacity:0.4},img5:{opacity:0.4}})
                break;
            case Math.floor(iP+(imgPercent*4)):
                setOpacity({img4:{opacity:0.4},img6:{opacity:0.4}})
                break;
            case Math.floor(iP+(imgPercent*5)):
                setOpacity({img5:{opacity:0.4},img1:{opacity:0.4}})
                break;
            case Math.floor(iP+(imgPercent*6)):
                setOpacity({img6:{opacity:0.4},img2:{opacity:0.4}})
                break;
        }
    },[index])
    
    return (
        <div className='Carousel'>
            
            <div className='Carousel-Header'>

                <span className='Carousel-Header-title'>Zombie Frogs Collection</span>
                <div className='Carousel-Divider'></div>
            </div>
            <div  className='Carousel-Body' id='Carousel-Body'>
                <div className='Carousel-InnerBody' style={animation}  id='Carousel-InnerBody'>
                    <img style={opacity.img5} alt="" src={img5} className="Carousel-BodyImage"></img>
                    <img style={opacity.img6} alt="" src={img6} className="Carousel-BodyImage"></img>
                    <img style={opacity.img1} alt="" src={img1} className="Carousel-BodyImage"></img>
                    <img style={opacity.img2} alt="" src={img2} className="Carousel-BodyImage"></img>
                    <img style={opacity.img3} alt="" src={img3} className="Carousel-BodyImage"></img>
                    <img style={opacity.img4} alt="" src={img4} className="Carousel-BodyImage"></img>
    
                    <img style={opacity.img5} alt="" src={img5} className="Carousel-BodyImage"></img>
                    <img style={opacity.img6} alt="" src={img6} className="Carousel-BodyImage"></img>
                    <img style={opacity.img1} alt="" src={img1} className="Carousel-BodyImage"></img>
                    <img style={opacity.img2} alt="" src={img2} className="Carousel-BodyImage"></img>
                    <img style={opacity.img3} alt="" src={img3} className="Carousel-BodyImage"></img>
                </div>
            </div>
          
        </div>
    )
}

export default Carousel



