import Header from './components/Header'
import Footer from './components/Footer'
import "./styles/Home.css"
import HomeBackgroundImage from './assets/9.png'
import Carousel from './components/Carousel'
import MoveModal from './misc/AnimateModal'

import { useEffect, useState } from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { Button, CircularProgress, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";

import * as anchor from "@project-serum/anchor";

import { LAMPORTS_PER_SOL } from "@solana/web3.js";

import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { WalletDialogButton } from "@solana/wallet-adapter-material-ui";

import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
  shortenAddress,
} from "./candy-machine";

const ConnectButton = styled(WalletDialogButton)``;

const CounterText = styled.span``; // add your styles here

const MintContainer = styled.div``; // add your styles here

const MintButton = styled(Button)``; // add your styles here

export interface HomeProps {
  candyMachineId: anchor.web3.PublicKey;
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number;
  treasury: anchor.web3.PublicKey;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  const [api_url, setUrl] = useState(process.env.REACT_APP_API_URL)
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT
  const [isWhitelisted, SetWhitelisted] = useState(false);

  const [itemsAvailable, setItemsAvailable] = useState(0);
  const [itemsRedeemed, setItemsRedeemed] = useState(0);
  const [itemsRemaining, setItemsRemaining] = useState(0);

  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });

  const [startDate, setStartDate] = useState(new Date(props.startDate));

  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState<CandyMachine>();
  const refreshCandyMachineState = () => {
    (async () => {
      if (!wallet) return;

      const {
        candyMachine,
        goLiveDate,
        itemsAvailable,
        itemsRemaining,
        itemsRedeemed,
      } = await getCandyMachineState(
        wallet as anchor.Wallet,
        props.candyMachineId,
        props.connection
      );

      setItemsAvailable(itemsAvailable);
      setItemsRemaining(itemsRemaining);
      setItemsRedeemed(itemsRedeemed);

      setIsSoldOut(itemsRemaining === 0);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);

    })();
  };

  const onMint = async () => {
    try {
      let res = await fetch(`${api_url}/whitelisted/member/${(wallet as anchor.Wallet).publicKey.toString()}`, {method: "GET"})
      const res_json = await res.json()
 
      const res_num = await JSON.parse(JSON.stringify(res_json)).reserve //The number  of reserves the user has left
    
      if(!isWhitelisted){
        throw new Error("You are not whitelisted");
      }
      if(res_num - 1 < 0){
        console.log("confirmed")
        throw new Error("Not enough reserves");
      }
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(
          candyMachine,
          props.config,
          wallet.publicKey,
          props.treasury
        );

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
          const to_send = await JSON.stringify({"reserve": res_num-1})
          await fetch(`${api_url}/whitelisted/update/${(wallet as anchor.Wallet).publicKey.toString()}/${process.env.REACT_APP_SECRET_KEY}`, {
            method: "PUT",
            headers: {
            'Content-Type': 'application/json',
            },
            body: to_send})
          console.log("Updated Reserves for user")

        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error: any) {
      // TODO: blech:
      let message = error.message || "Minting failed! Please try again!";
      if (!error.message) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        } else if (error.message === "You are not whitelisted"){
          message = error.message;
        } else if (error.message === "Not enough reserves"){
          message = error.message
        }
        else {
          message = "You are not whitlisted";}
        }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
      refreshCandyMachineState();
    }
  };

  useEffect(() => {
    (async () => {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
        const data = await fetch(`${api_url}/whitelisted/member/${(wallet as anchor.Wallet).publicKey.toString()}`)
        if(data.status.toString() !== "404"){
          SetWhitelisted(true)
        }
        else{
          console.log("not found")
        }
      }
    })();
  }, [wallet, props.connection]);

  useEffect(refreshCandyMachineState, [
    wallet,
    props.candyMachineId,
    props.connection,
  ]);

  // useEffect(()=>{
  //     MoveModal();
  // },[]);


  const[days,setDays]=useState('')
  const[hours,setHours]=useState('')
  const[minutes,setMinutes]=useState('')
  const[seconds,setSeconds]=useState('')
  const[timerFinished,setTimerFinished]=useState(false)
  const[visible,setVisible]=useState(false)
  // Update the count down every 1 second
  useEffect(()=>{
    var countDownDate = new Date(Date.UTC(2021,9,29,2)).getTime();

    var timer = setInterval(function() {
  
      // Get today's date and time
      var now = new Date().getTime();
     
      // Find the distance between now and the count down date
      var distance = countDownDate - now;
    
      // Time calculations for days, hours, minutes and seconds
      var days = Math.floor(distance / (1000 * 60 * 60 * 24));
      var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      var seconds = Math.floor((distance % (1000 * 60)) / 1000);
      setVisible(true)

      if(seconds>9){
        setSeconds(`${seconds}`)
      }
      else{
        setSeconds(`0${seconds}`)
      }
      if(minutes>9){
        setMinutes(`${minutes}`)
      }
      else{
        setMinutes(`0${minutes}`)
      }
      if(hours>9){
        setHours(`${hours}`)
      }
      else{
        setHours(`0${hours}`)
      }
      if(days>9){
        setDays(`${days}`)
      }
      else{
        setDays(`0${days}`)
      }
  
      if (distance < 0) {
        clearInterval(timer);
        setTimerFinished(true)
      }

    }, 1000);
  
  },[])
  



  return (
      <div className='Home'>
          <Header/>
          <div className='Home-body'>
              <div className='Home-backgroundImageContainer'>
                  <img alt="" className='Home-backgroundImage' src={HomeBackgroundImage}></img>
                  <div  className='home-bodyModal-Desktop' id='home-bodyModal-Desktop' >
                    <h1 className='home-bodyModalTitle'>The First Apocalyptic Frog NFT on the Solana Network</h1>
                      <span className='home-bodyModalText'>ZombieFrogs NFT</span>
                      {timerFinished===false&&visible===true?
                        <div className='timer'>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                              {days}
                            </div>
                            <span>DAYS</span>
                          </div>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                              {hours}
                            </div>
                            <span>HOURS</span>
                          </div>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                              {minutes}
                            </div>
                            <span>MINS</span>
                          </div>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                                {seconds}
                            </div>
                            <span>SECS</span>
                          </div>

                        </div>    
                      :timerFinished===true&&visible===true?
                       <a className='home-bodyModalButton' >
                          <main>
                          {wallet && (
                          <p>Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}</p>
                          )}

                          {wallet && <p>Balance: {(balance || 0).toLocaleString()} SOL</p>}

                          {wallet && <p>Total Available: {itemsAvailable}</p>}

                          {wallet && <p>Redeemed: {itemsRedeemed}</p>}

                          {wallet && <p>Remaining: {itemsRemaining}</p>}

                          <MintContainer>
                          {!wallet ? (
                          <ConnectButton>Connect Wallet</ConnectButton>
                          ) : (
                          <MintButton
                          disabled={isSoldOut || isMinting || !isActive}
                          onClick={onMint}
                          variant="contained"
                          >
                          {isSoldOut ? (
                          "SOLD OUT"
                          ) : isActive ? (
                          isMinting ? (
                          <CircularProgress />
                          ) : (
                          "MINT"
                          )
                          ) : (
                          <Countdown
                          date={startDate}
                          onMount={({ completed }) => completed && setIsActive(true)}
                          onComplete={() => setIsActive(true)}
                          renderer={renderCounter}
                          />
                          )}
                          </MintButton>
                          )}
                          </MintContainer>

                          <Snackbar
                          open={alertState.open}
                          autoHideDuration={6000}
                          onClose={() => setAlertState({ ...alertState, open: false })}
                          >
                          <Alert
                          onClose={() => setAlertState({ ...alertState, open: false })}
                          severity={alertState.severity}
                          >
                          {alertState.message}
                          </Alert>
                          </Snackbar>
                          </main>
                      </a> 
                    :null}
                  </div>
              </div>

              <div className='Home-socialDiv'>
                <div className='home-bodyModal-Mobile' >
                    <h1 className='home-bodyModalTitle'>The First Apocalyptic Frog NFT on the Solana Network</h1>
                    <span className='home-bodyModalText'>ZombieFrogs NFT</span>
                    {timerFinished===false&&visible===true?
                        <div className='timer'>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                              {days}
                            </div>
                            <span>DAYS</span>
                          </div>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                              {hours}
                            </div>
                            <span>HOURS</span>
                          </div>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                              {minutes}
                            </div>
                            <span>MINS</span>
                          </div>
                          <div className='timer-child'>
                            <div className='timer-childInner'>
                                {seconds}
                            </div>
                            <span>SECS</span>
                          </div>

                        </div>    
                      :timerFinished===true&&visible===true?
                       <a className='home-bodyModalButton' >
                          <main>
                          {wallet && (
                          <p>Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}</p>
                          )}

                          {wallet && <p>Balance: {(balance || 0).toLocaleString()} SOL</p>}

                          {wallet && <p>Total Available: {itemsAvailable}</p>}

                          {wallet && <p>Redeemed: {itemsRedeemed}</p>}

                          {wallet && <p>Remaining: {itemsRemaining}</p>}

                          <MintContainer>
                          {!wallet ? (
                          <ConnectButton>Connect Wallet</ConnectButton>
                          ) : (
                          <MintButton
                          disabled={isSoldOut || isMinting || !isActive}
                          onClick={onMint}
                          variant="contained"
                          >
                          {isSoldOut ? (
                          "SOLD OUT"
                          ) : isActive ? (
                          isMinting ? (
                          <CircularProgress />
                          ) : (
                          "MINT"
                          )
                          ) : (
                          <Countdown
                          date={startDate}
                          onMount={({ completed }) => completed && setIsActive(true)}
                          onComplete={() => setIsActive(true)}
                          renderer={renderCounter}
                          />
                          )}
                          </MintButton>
                          )}
                          </MintContainer>

                          <Snackbar
                          open={alertState.open}
                          autoHideDuration={6000}
                          onClose={() => setAlertState({ ...alertState, open: false })}
                          >
                          <Alert
                          onClose={() => setAlertState({ ...alertState, open: false })}
                          severity={alertState.severity}
                          >
                          {alertState.message}
                          </Alert>
                          </Snackbar>
                          </main>
                      </a> 
                    :null}
                </div>
                <div>
                    <h2 className='Home-socialDivText'>Social</h2>
                </div>
                <div className='Home-socialDivDivider'></div>
                <div className='Home-socialDivIconHolder' >
                    <a className='Home-socialDivIcon' href='https://twitter.com/frogs_nft'>
                          <img src="https://img.icons8.com/ios-glyphs/22/000000/twitter--v1.png"/>
                    </a>
                    
                    <a className='Home-socialDivIcon2' href='https://discord.gg/Z7JyqKvPDM'>
                        <img src="https://img.icons8.com/ios-filled/25/000000/discord--v1.png"/>
                    </a>
                </div>
              </div>
              <Carousel/>
          </div>

          <Footer/>
      </div>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

const renderCounter = ({ days, hours, minutes, seconds, completed }: any) => {
  return (
    <CounterText>
      {hours + (days || 0) * 24} hours, {minutes} minutes, {seconds} seconds
    </CounterText>
  );
};

export default Home;
